<template>
	<div class="yu-color-panel-wrapper">
    <div class="yu-color-panel">
      <div class="yu-color-panel-hd">
        <div class="color">
          <span v-for="(color,index) in colors" :key="index" v-text="(colors.length - 1) === index ?color:color + ' - '"></span>
        </div>
        <div class="copy" v-copy="css">{{text}}</div>
      </div>
      <div class="yu-color-panel-bd">
        <div class="circle" :style="css"></div>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'ColorPanel',
		props: {
      colors: {
				type: Array,
				default () {
					return []
				}
			},
      css: {
				type: String,
				default () {
					return ''
				}
			}
		},
		data() {
			return {
        text:"Copy CSS"
			}
		},
		watch: {

		},
		methods: {
			onClick(){
				this.$emit('click')
			},
      callback(){
        return ()=>{
          console.log('复制成功2')
          this.text = "Copied";
          setTimeout(()=>{
            this.text = "Copy CSS";
          })
        }
      }
		}
	}
</script>
<style lang="scss" scoped>
.yu-color-panel-wrapper{
  .yu-color-panel{
    background-color: #fff;
    border-radius: 8px;
    padding: 8px 15px;
    .yu-color-panel-hd{
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      cursor: pointer;
      font-size: 12px;
      .color{
        -webkit-flex: 1;
        -ms-flex: 1;
        -webkit-box-flex: 1;
        -moz-box-flex: 1;
        flex: 1;
        width: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        span{
          color: #868e96;
        }
      }
      .copy{
        color: #868e96;
      }

      .copy:hover{
        color: #507bf4;
      }
    }
    .yu-color-panel-bd{
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px 0;
      .circle{
        width: 120px;
        height: 120px;
        border-radius: 120px;
      }
    }
  }
}
@media screen and (max-width: 576px) {
  .yu-color-panel-wrapper{
    .yu-color-panel{
      .yu-color-panel-hd{
        cursor: auto;
        .copy:hover{
          color: #868e96;
        }
      }
    }
  }
}
</style>
